"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription } from "@/components/ui/alert"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

interface Signup {
  name: string
  email: string
  phone: string
  message: string
  timestamp: string
}

export default function Admin() {
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [password, setPassword] = useState("")
  const [signups, setSignups] = useState<Signup[]>([])
  const [error, setError] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)
    setError(null)

    try {
      const response = await fetch("/api/admin/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ password }),
      })

      if (!response.ok) {
        throw new Error("Invalid password")
      }

      setIsAuthenticated(true)
      setPassword("")
      fetchSignups()
    } catch (error) {
      setError("Invalid password. Please try again.")
    } finally {
      setIsLoading(false)
    }
  }

  const fetchSignups = async () => {
    try {
      const response = await fetch("/api/admin/signups")
      if (!response.ok) {
        throw new Error("Failed to fetch signups")
      }
      const data = await response.json()
      setSignups(data.signups)
    } catch (error) {
      setError("Failed to fetch signup requests")
    }
  }

  const handleLogout = () => {
    setIsAuthenticated(false)
    setSignups([])
  }

  if (!isAuthenticated) {
    return (
      <div className="max-w-md mx-auto">
        <Card>
          <CardHeader>
            <CardTitle>Admin Login</CardTitle>
          </CardHeader>
          <CardContent>
            {error && (
              <Alert className="mb-6 bg-red-50">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            <form onSubmit={handleLogin} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="password">Password</Label>
                <Input
                  id="password"
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  placeholder="Enter admin password"
                />
              </div>
              <Button type="submit" className="w-full" disabled={isLoading}>
                {isLoading ? "Logging in..." : "Login"}
              </Button>
            </form>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Service Requests</h1>
        <Button variant="outline" onClick={handleLogout}>
          Logout
        </Button>
      </div>

      {error && (
        <Alert className="bg-red-50">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <Card>
        <CardContent className="p-0">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Name</TableHead>
                <TableHead>Email</TableHead>
                <TableHead>Phone</TableHead>
                <TableHead>Message</TableHead>
                <TableHead>Date</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {signups.map((signup, index) => (
                <TableRow key={index}>
                  <TableCell>{signup.name}</TableCell>
                  <TableCell>{signup.email}</TableCell>
                  <TableCell>{signup.phone}</TableCell>
                  <TableCell className="max-w-xs truncate">{signup.message}</TableCell>
                  <TableCell>{new Date(signup.timestamp).toLocaleDateString()}</TableCell>
                </TableRow>
              ))}
              {signups.length === 0 && (
                <TableRow>
                  <TableCell colSpan={5} className="text-center py-4">
                    No service requests found
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  )
}
